* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.html,
body {
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background: #232526; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #414345,
    #232526
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #414345,
    #232526
  );
  background: linear-gradient(
    to right,
    #414345,
    #232526
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.Component {
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #1565c0,
    #b92b27
  );
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  padding: 15px;
  border-radius: 15px;
  border: 1px solid #b92b27;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.Component {
  margin-top: 15px;
}

.SampleContainer {
  background: coral;
  border-radius: 15px;
  width: 75%;
  margin: 0 auto;
  padding: 2% 5%;
  position: relative;
}

.SampleContainer h1 {
  text-align: center;
  border-radius: 15px;
  -webkit-transition: all 1s;
  transition: all 1s;
  width: 100%;
}

.SampleContainer:hover h1 {
  color: black;
  -webkit-transform: rotate(-10deg) scale(1.4);
          transform: rotate(-10deg) scale(1.4);
}

.btn {
  padding: 20px;
  background: #808080; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3fada8,
    #808080
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #3fada8,
    #808080
  );
  background: linear-gradient(
    to right,
    #3fada8,
    #808080
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  border-radius: 15px;
  margin: 15px auto;
  display: block;
  width: 50%;
  min-width: 150px;
  font-size: 22px;
  color: black;
  letter-spacing: 2px;
  font-weight: bold;
  text-decoration: none;
}

.Layout {
  height: 100vh;

  text-align: center;
}

h1 {
  padding: 5% 0;
  color: white;
}

